import { createContext, useContext, useReducer, useState } from "react";
import BreadCrumbs from "../../component/BreadCrumbs";
import { Outlet } from "react-router-dom";
import { Box, Button, CircularProgress } from "@mui/material";
import { green } from "@mui/material/colors";
import { pink } from "@mui/material/colors";
import AddIcon from "@mui/icons-material/Add";
import { AuthContext } from "../../../../../../../components/AuthContext";
import useUrlParams from "../../../../../../../hooks/useUrlParams";
import { useQuery, useMutation, useQueryClient } from "react-query";
import CloseIcon from "@mui/icons-material/Close";
import SaveIcon from "@mui/icons-material/Save";
import Swal from "sweetalert2";
import DeleteIcon from "@mui/icons-material/Delete";
import { GridRowSelectionModel } from "@mui/x-data-grid";
import { FormControl, InputLabel, Select, MenuItem } from "@mui/material";
import ContentPasteSearchIcon from "@mui/icons-material/ContentPasteSearch";
import ModalWithTable from "../../../../../../../components/ModalWithTable";

const VehiclePolicyComponents = [
  { link: "/dashboard/task/production/policy/", text: "Policy Information" },
  {
    link: "/dashboard/task/production/policy/vehicle/policy-type-details",
    text: "Policy Type and Details",
  },
  {
    link: "/dashboard/task/production/policy/vehicle/policy-premium",
    text: "Policy Premium",
  },
];

const jsongStringInitialState: any = {
  form_action: "REG",
  form_type: "COM",
  sub_account: "HO",
  //insurer info
  client_id: "",
  client_name: "",
  client_address: "",
  //agent info
  agent_id: "",
  agent_name: "",
  agent_com: "0.00",
  //Vehicle policy
  PolicyAccount: "",
  PolicyNo: "",
  CCN: "",
  ORN: "",
  rateCost: "",
  //Period Insurance
  DateFrom: new Date(),
  DateTo: new Date(),
  DateIssued: new Date(),
  //Insured Unit
  Model: "charles1",
  Make: "",
  TB: "",
  Color: "",
  BLTFileNo: "",
  PlateNo: "",
  ChassisNo: "",
  MotorNo: "",
  AuthorizedCapacity: "",
  UnladenWeigth: "",

  //==========================
  //tpl
  TplType: JSON.stringify({ Account: "", PremuimPaid: "0.00" }),
  PremiumPaid: "0.00",
  //compre
  EVSV: "0.00",
  Aircon: "0.00",
  Stereo: "0.00",
  Magwheels: "0.00",
  OthersRate: "0.00",
  OthersDesc: "",
  CompreType: "",

  Deductible: "0.00",
  Towing: "0.00",
  ARL: "0.00",
  BodyInjury: "0.00",
  PropertyDamage: "0.00",
  PersinalAccident: "0.00",
  Denomination: "",

  //==========================
  //mortgage
  Mortgagee: "",
  MortgageeForm: "false",
  //Premiums
  SectionI_II: "0.00",
  SectionIII: "0.00",
  OwnDamage: "0.00",
  Theft: "0.00",
  SectionIVA: "0.00",
  SectionIVB: "0.00",
  PremiumOther: "0.00",
  AOG: "0.00",
  AOGPercent: "0.00",
  TotalPremium: "0.00",
  Vat: "0.00",
  DocStamp: "0.00",
  LocalGovTaxPercent: "0.75",
  LocalGovTax: "0.00",
  StradCom: "0.00",
  TotalDue: "0.00",
  Type: "charles1",
  Source_No_Ref_ID: "",

  // extra
  mode: "",
};

const reducer = (state: any, action: any) => {
  switch (action.type) {
    case "UPDATE_FIELD":
      const newState = {
        ...state,
        [action.field]: action.value,
      };
      localStorage.setItem("VPolicyInitialState", JSON.stringify(newState));
      return newState;
    default:
      return state;
  }
};

export const VehicleContext = createContext<any>({});

const initialStateJSON = JSON.stringify(jsongStringInitialState);
const storedInitialStateJSON = localStorage.getItem("VPolicyInitialState");

const initialStateStored = storedInitialStateJSON
  ? storedInitialStateJSON
  : initialStateJSON;
localStorage.setItem("VPolicyInitialState", initialStateStored);

const initialState = (localStorage.getItem("VPolicyInitialState") as string)
  ? JSON.parse(localStorage.getItem("VPolicyInitialState") as string)
  : jsongStringInitialState;
const queryKeySearch = "vehicle-policy-search";
const queryKeyUpdateAdd = "vehicle-policy-search";
const queryKeyGet = "vehicle-policy-get";
const querySearchPolicyIds = "vehicle-policy-search-ppolicy-id";

export const vpolicyColumn = [
  { field: "DateIssued", headerName: "Date", width: 200 },
  { field: "PolicyNo", headerName: "Policy No", width: 170 },
  {
    field: "Account",
    headerName: "Account",
    width: 170,
  },
  {
    field: "client_fullname",
    headerName: "Full Name",
    flex: 1,
  },
];

const vpolicyKey = "vehicle-policy";

export default function VehiclePolicy() {
  const [state, dispatch] = useReducer(reducer, initialState);
  const { myAxios, user } = useContext(AuthContext);
  const { searchParams } = useUrlParams();
  const [clientRows, setClientRows] = useState<GridRowSelectionModel>([]);
  const [agentRows, setAgentRows] = useState<GridRowSelectionModel>([]);
  const [tplId, setTplId] = useState<GridRowSelectionModel>([]);
  const [search, setSearch] = useState<GridRowSelectionModel>([]);
  const [searchShow, setSearchShow] = useState(false);
  const [loadSearchingChange, setLoadSearchingChange] = useState(false);
  const [Mortgagee, setMortgagee] = useState(false);
  const [showField, setShowField] = useState({
    thirdparty: state.form_type.toLowerCase() === "tpl",
    compre: state.form_type.toLowerCase() === "com",
  });
  const queryClient = useQueryClient();
  const isAddOrEditMode = state.mode === "";
  const { data: tplData, isLoading: tplLoading } = useQuery({
    queryKey: querySearchPolicyIds,
    queryFn: async () =>
      await myAxios.get(
        `/task/production/tpl-ids-vehicle-policy?tplIDSearch=`,
        {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        }
      ),
    onSuccess: (res) => {
      const response = res as any;
     
      setTplId(response.data);
    },
  });
  const { isLoading: searchLoading } = useQuery({
    queryKey: queryKeySearch,
    queryFn: async () =>
      await myAxios.get(
        `/task/production/tpl-search-vehicle-policy?form_type=${state.form_type}&form_action=${state.form_action}&search=`,
        {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        }
      ),
    onSuccess: (res) => {
      const response = res.data?.searchVPolicy as any;
      setSearch(response);
    },
  });
  const { data, isLoading } = useQuery({
    queryKey: queryKeyGet,
    queryFn: async () =>
      await myAxios.get(
        `/task/production/get-vehicle-policy?vpolicySearch=${
          searchParams.get("vpolicySearch") as string
        }`,
        {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        }
      ),
    onSuccess: (res) => {
      const response = res as any;
     
      setClientRows(response?.data.vehiclePolicy?.clients);
      setAgentRows(response?.data.vehiclePolicy?.agents);
      if (state.form_action === "TEMP") {
        handleDateChange(
          response.data.vehiclePolicy.tempPolicyId[0].tempPolicy_No,
          "PolicyNo"
        );
      }
    },
  });

  const {
    mutate,
    isLoading: loadingAddNew,
    variables,
  } = useMutation({
    mutationKey: queryKeyUpdateAdd,
    mutationFn: async (variables: any) => {
      if (state.mode === "delete") {
        return await myAxios.post(
          "/task/production/tpl-update-vehicle-policy",
          variables,
          {
            headers: {
              Authorization: `Bearer ${user?.accessToken}`,
            },
          }
        );
      }
      return await myAxios.post(
        "/task/production/tpl-add-vehicle-policy",
        variables,
        {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        }
      );
    },
    onSuccess: (res) => {
      if (res.data.success) {
        Promise.all([
          queryClient.invalidateQueries(queryKeySearch),
          queryClient.invalidateQueries(queryKeyUpdateAdd),
          queryClient.invalidateQueries(queryKeyGet),
          queryClient.invalidateQueries(querySearchPolicyIds),
        ]);
        return Swal.fire({
          position: "center",
          icon: "success",
          title: res.data.message,
          showConfirmButton: false,
          timer: 1500,
        }).then(() => {
          if (state.form_action === "TEMP") {
            const getNumbers = state.PolicyNo.split("TP-")[1];
            const inc = parseInt(getNumbers) + 1;
            const getZero = getNumbers.slice(0, getNumbers.length - 1);
            jsongStringInitialState.PolicyNo = `TP-${getZero}${inc}`;
            jsongStringInitialState.form_action = "TEMP";
          }
          jsongStringInitialState.form_type = state.form_type;
          backToDefaultState(jsongStringInitialState);
        });
      }

      Swal.fire({
        position: "center",
        icon: "error",
        title: res.data.message,
        showConfirmButton: false,
        timer: 1500,
      });
    },
  });
  const { mutate: mutateDelete, isLoading: loadingDelete } = useMutation({
    mutationKey: queryKeyUpdateAdd,
    mutationFn: async (variables: any) => {
      return await myAxios.post(
        "/task/production/tpl-delete-vehicle-policy",
        variables,
        {
          headers: {
            Authorization: `Bearer ${user?.accessToken}`,
          },
        }
      );
    },
    onSuccess: (res) => {
      if (res.data.success) {
        Promise.all([
          queryClient.invalidateQueries(queryKeySearch),
          queryClient.invalidateQueries(queryKeyUpdateAdd),
          queryClient.invalidateQueries(queryKeyGet),
          queryClient.invalidateQueries(querySearchPolicyIds),
        ]);
        return Swal.fire({
          position: "center",
          icon: "success",
          title: res.data.message,
          showConfirmButton: false,
          timer: 1500,
        }).then(() => {
          backToDefaultState(jsongStringInitialState, true);
        });
      }

      Swal.fire({
        position: "center",
        icon: "error",
        title: res.data.message,
        showConfirmButton: false,
        timer: 1500,
      });
    },
  });

  const handleInputChange = (e: any) => {
    const { name, value } = e.target;
    dispatch({ type: "UPDATE_FIELD", field: name, value });
  };

  const handleDateChange = (value: any, name: string) => {
    dispatch({ type: "UPDATE_FIELD", field: name, value });
  };

  function handleOnSave() {
    if (state.client_id === "" || state.client_id === null) {
      return Swal.fire(
        "Unable to save! Invalid IDNo.",
        "you missed the Client Id Field?",
        "error"
      );
    }
    if (state.PolicyAccount === "" || state.PolicyAccount === null) {
      return Swal.fire(
        "Unable to save! Please select Account.",
        "you missed the Account Field?",
        "error"
      );
    }
    if (state.PolicyNo === "" || state.PolicyNo === null) {
      return Swal.fire(
        "Unable to save! Invalid Policy No.",
        "you missed the Policy No Field?",
        "error"
      );
    }
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, Save it!",
    }).then((result) => {
      if (result.isConfirmed) {
        state.isUpdate = false;
        mutate(state);
      }
    });
  }
  function tplCompuation() {
    if (isNaN(parseFloat(state.PremiumPaid))) {
      return Swal.fire({
        position: "center",
        icon: "error",
        title: "Premiumn Paid is Required",
        showConfirmButton: false,
        timer: 1500,
      });
    }
    const vatPercentage = 12 / 100;
    const dsPercentage = 12.5 / 100;
    const lgtPercentage = 0.75 / 100;
    const vatResult = vatPercentage * parseFloat(state.PremiumPaid);
    const dsResult = dsPercentage * parseFloat(state.PremiumPaid);
    const lgtResult = lgtPercentage * parseFloat(state.PremiumPaid);

    if (isNaN(parseFloat(state.StradCom))) {
      state.StradCom = "0.00";
    }
    const totalResult =
      vatResult +
      dsResult +
      lgtResult +
      parseFloat(state.StradCom) +
      parseFloat(state.PremiumPaid);

    handleDateChange(
      `${parseFloat(state.PremiumPaid).toFixed(2)}`,
      "SectionI_II"
    );
    handleDateChange(
      `${parseFloat(state.PremiumPaid).toFixed(2)}`,
      "TotalPremium"
    );
    handleDateChange(`${vatResult.toFixed(2)}`, "Vat");
    handleDateChange(`${dsResult.toFixed(2)}`, "DocStamp");
    handleDateChange(`${lgtResult.toFixed(2)}`, "LocalGovTax");
    handleDateChange(`${totalResult.toFixed(2)}`, "TotalDue");
  }
  function comComputation() {
    if (isNaN(parseFloat(state.SectionIII))) {
      state.SectionIII = "0.00";
    }
    if (isNaN(parseFloat(state.AOGPercent))) {
      state.AOGPercent = "0.00";
    }
    if (isNaN(parseFloat(state.Theft))) {
      state.Theft = "0.00";
    }
    if (isNaN(parseFloat(state.SectionIVA))) {
      state.SectionIVA = "0.00";
    }
    if (isNaN(parseFloat(state.SectionIVB))) {
      state.SectionIVB = "0.00";
    }
    if (isNaN(parseFloat(state.PremiumOther))) {
      state.PremiumOther = "0.00";
    }
    if (isNaN(parseFloat(state.AOG))) {
      state.AOG = "0.00";
    }
    if (isNaN(parseFloat(state.LocalGovTaxPercent))) {
      state.LocalGovTaxPercent = "0.00";
    }
    if (isNaN(parseFloat(state.Vat))) {
      state.Vat = "0.00";
    }
    if (isNaN(parseFloat(state.DocStamp))) {
      state.DocStamp = "0.00";
    }
    if (isNaN(parseFloat(state.LocalGovTax))) {
      state.LocalGovTax = "0.00";
    }
    if (isNaN(parseFloat(state.StradCom))) {
      state.StradCom = "0.00";
    }

    const vatPercentage = 12 / 100;
    const docPercentage = 12.5 / 100;
    const lgtPercentage = parseFloat(state.LocalGovTaxPercent) / 100;

    const section = parseFloat(state.SectionIII) / 100;
    const aogPercent = parseFloat(state.AOGPercent) / 100;
    const owmDamageResult = parseFloat(state.EVSV) * section;
    const aogDamageResult = parseFloat(state.EVSV) * aogPercent;
    handleDateChange(`${owmDamageResult.toFixed(2)}`, "OwnDamage");
    handleDateChange(`${aogDamageResult.toFixed(2)}`, "AOG");
    const totalPremiumResult =
      parseFloat(owmDamageResult.toFixed(2)) +
      parseFloat(aogDamageResult.toFixed(2)) +
      parseFloat(state.SectionIVB) +
      parseFloat(state.PremiumOther) +
      parseFloat(state.SectionIVA);
    handleDateChange(
      `${(vatPercentage * totalPremiumResult).toFixed(2)}`,
      "Vat"
    );
    handleDateChange(
      `${(docPercentage * totalPremiumResult).toFixed(2)}`,
      "DocStamp"
    );
    handleDateChange(
      `${(lgtPercentage * totalPremiumResult).toFixed(2)}`,
      "LocalGovTax"
    );

    handleDateChange("0.00", "TotalPremium");
    handleDateChange("0.00", "TotalDue");

    const totalDuePremiumResult =
      totalPremiumResult +
      parseFloat((vatPercentage * totalPremiumResult).toFixed(2)) +
      parseFloat((docPercentage * totalPremiumResult).toFixed(2)) +
      parseFloat((lgtPercentage * totalPremiumResult).toFixed(2)) +
      parseFloat(parseFloat(state.StradCom).toFixed(2));

    handleDateChange(`${totalPremiumResult.toFixed(2)}`, "TotalPremium");
    handleDateChange(`${totalDuePremiumResult.toFixed(2)}`, "TotalDue");
  }
  function backToDefaultState(json: any, resetAll: boolean = false) {
    json.form_type = state.form_type;
    json.form_action = state.form_action;
    if (!resetAll) {
      json.mode = state.mode;
    }
    Object.entries(json).forEach(([key, value]) => {
      handleDateChange(value, key);
    });
  }

  if (isLoading) {
    return <h1>Loading....</h1>;
  }
  return (
    <VehicleContext.Provider
      value={{
        state,
        handleInputChange,
        handleDateChange,
        Mortgagee,
        setMortgagee,
        showField,
        setShowField,
        isLoading,
        clientRows,
        setClientRows,
        myAxios,
        user,
        agentRows,
        setAgentRows,
        data,
        tplCompuation,
        comComputation,
        tplLoading,
        tplData,
        tplId,
        setTplId,
        isAddOrEditMode,
      }}
    >
      <BreadCrumbs list={VehiclePolicyComponents} />
      <Box
        sx={(theme) => ({
          display: "flex",
          alignItems: "center",
          columnGap: "20px",
          marginBottom: "10px",
          [theme.breakpoints.down("sm")]: {
            flexDirection: "column",
            alignItems: "flex-start",
            flex: 1,
          },
        })}
      >
        <div
          style={{
            marginTop: "10px",
            marginBottom: "12px",
            flex: 1,
            width: "100%",
          }}
        ></div>
        <div
          style={{
            display: "flex",
            alignItems: "center",
            columnGap: "20px",
          }}
        >
          <div
            style={{
              display: "flex",
              alignItems: "center",
              columnGap: "5px",
            }}
          >
            {state.mode === "" && (
              <Button
                variant="contained"
                startIcon={<AddIcon />}
                onClick={() => {
                  handleDateChange("add", "mode");
                }}
              >
                New
              </Button>
            )}

            <Box sx={{ position: "relative" }}>
              <Button
                color="primary"
                variant="contained"
                type="submit"
                onClick={handleOnSave}
                disabled={loadingAddNew || state.mode === ""}
                startIcon={<SaveIcon />}
              >
                Save
              </Button>

              {loadingAddNew && variables.mode === undefined && (
                <CircularProgress
                  size={24}
                  sx={{
                    color: green[500],
                    position: "absolute",
                    top: "50%",
                    left: "50%",
                    marginTop: "-12px",
                    marginLeft: "-12px",
                  }}
                />
              )}
            </Box>
            {state.mode !== "" && (
              <Button
                variant="contained"
                startIcon={<CloseIcon />}
                color="error"
                onClick={() => {
                  Swal.fire({
                    title: "Are you sure?",
                    text: "You won't be able to revert this!",
                    icon: "warning",
                    showCancelButton: true,
                    confirmButtonColor: "#3085d6",
                    cancelButtonColor: "#d33",
                    confirmButtonText: "Yes, cancel it!",
                  }).then((result) => {
                    if (result.isConfirmed) {
                      handleDateChange("", "mode");
                      jsongStringInitialState.form_action = state.form_action;
                      backToDefaultState(jsongStringInitialState, true);
                    }
                  });
                }}
              >
                Cancel
              </Button>
            )}
            <Box sx={{ position: "relative" }}>
              <Button
                id="save-entry-header"
                variant="contained"
                sx={{
                  backgroundColor: pink[500],
                  "&:hover": {
                    backgroundColor: pink[600],
                  },
                }}
                disabled={state.mode !== "delete"}
                startIcon={<DeleteIcon />}
                onClick={() => {
                  Swal.fire({
                    title: "Are you sure?",
                    text: "You won't be able to revert this!",
                    icon: "warning",
                    showCancelButton: true,
                    confirmButtonColor: "#3085d6",
                    cancelButtonColor: "#d33",
                    confirmButtonText: "Yes, delete it!",
                  }).then((result) => {
                    if (result.isConfirmed) {
                      mutateDelete({
                        PolicyAccount: state.PolicyAccount,
                        form_type: state.form_type,
                        PolicyNo: state.PolicyNo,
                      });
                    }
                  });
                }}
              >
                Delete
              </Button>

              {loadingDelete && (
                <CircularProgress
                  size={24}
                  sx={{
                    color: green[500],
                    position: "absolute",
                    top: "50%",
                    left: "50%",
                    marginTop: "-12px",
                    marginLeft: "-12px",
                  }}
                />
              )}
            </Box>
          </div>
        </div>
      </Box>
      <div style={{ marginBottom: "5px", display: "flex", gap: "10px" }}>
        <Button
          size="small"
          variant="outlined"
          startIcon={<ContentPasteSearchIcon />}
          onClick={() => {
            setSearchShow(true);
          }}
          disabled={searchLoading || loadSearchingChange}
        >
          Search Policy
        </Button>
        <FormControl
          size="small"
          sx={(theme) => ({
            width: "150px",
          })}
        >
          <InputLabel id="subAccount">Sub Account</InputLabel>
          <Select
            size="small"
            labelId="subAccount"
            label="subAccount"
            name="sub_account"
            value={state.sub_account}
            onChange={(e) => {
              handleDateChange("", "Denomination");
              handleDateChange("", "PolicyAccount");
              handleDateChange("", "Mortgagee");
              handleInputChange(e);
            }}
          >
            {(data?.data.vehiclePolicy.sub_account).map(
              (items: any, idx: number) => {
                return (
                  <MenuItem key={idx} value={items.Acronym.trim()}>
                    {items.Acronym}
                  </MenuItem>
                );
              }
            )}
          </Select>
        </FormControl>
        <FormControl
          size="small"
          sx={(theme) => ({
            width: "100px",
          })}
        >
          <Select
            labelId="formType"
            name="form_type"
            value={state.form_type}
            onChange={(e) => {
              Swal.fire({
                title: "Are you sure?",
                text: "You won't be able to revert this!",
                icon: "warning",
                showCancelButton: true,
                confirmButtonColor: "#3085d6",
                cancelButtonColor: "#d33",
                confirmButtonText: "Yes, change it!",
              }).then((result) => {
                if (result.isConfirmed) {
                  setLoadSearchingChange(true);
                  jsongStringInitialState.form_action = state.form_action;
                  backToDefaultState(jsongStringInitialState);
                  setShowField({
                    thirdparty: e.target.value.toLowerCase() === "tpl",
                    compre: e.target.value.toLowerCase() === "com",
                  });

                  myAxios
                    .get(
                      `/task/production/tpl-search-vehicle-policy?form_type=${e.target.value}&form_action=${e.target.value}&search=`,
                      {
                        headers: {
                          Authorization: `Bearer ${user?.accessToken}`,
                        },
                      }
                    )
                    .then((res) => {
                      const response = res.data?.searchVPolicy as any;
                      setSearch(response);
                      setLoadSearchingChange(false);
                    });

                  handleInputChange(e);
                }
              });
            }}
          >
            {[
              { Account: "TPL", show: state.form_action === "REG" },
              { Account: "COM", show: true },
            ].map((items: any, idx: number) => {
              return items.show ? (
                <MenuItem key={idx} value={items.Account}>
                  {items.Account}
                </MenuItem>
              ) : null;
            })}
          </Select>
        </FormControl>
        <FormControl
          size="small"
          sx={(theme) => ({
            width: "100px",
          })}
        >
          <Select
            name="form_action"
            value={state.form_action}
            onChange={(e) => {
              Swal.fire({
                title: "Are you sure?",
                text: "You won't be able to revert this!",
                icon: "warning",
                showCancelButton: true,
                confirmButtonColor: "#3085d6",
                cancelButtonColor: "#d33",
                confirmButtonText: "Yes, change it!",
              }).then((result) => {
                if (result.isConfirmed) {
                  setLoadSearchingChange(true);
                  jsongStringInitialState.form_type = "COM";
                  backToDefaultState(jsongStringInitialState);
                  if (e.target.value === "TEMP") {
                    handleDateChange(
                      (data as any)?.data.vehiclePolicy.tempPolicyId[0]
                        .tempPolicy_No,
                      "PolicyNo"
                    );
                  }
                  myAxios
                    .get(
                      `/task/production/tpl-search-vehicle-policy?form_type=${state.form_type}&form_action=${e.target.value}&search=`,
                      {
                        headers: {
                          Authorization: `Bearer ${user?.accessToken}`,
                        },
                      }
                    )
                    .then((res) => {
                      setLoadSearchingChange(false);
                      const response = res.data?.searchVPolicy as any;
                      setSearch(response);
                    });
                  handleInputChange(e);
                }
              });
            }}
          >
            {[{ Account: "REG" }, { Account: "TEMP" }].map(
              (items: any, idx: number) => {
                return (
                  <MenuItem key={idx} value={items.Account}>
                    {items.Account}
                  </MenuItem>
                );
              }
            )}
          </Select>
        </FormControl>
      </div>
      <Outlet />

      <ModalWithTable
        showModal={searchShow}
        onCloseModal={() => {
          setSearchShow(false);
        }}
        onClickCloseIcon={() => {
          setSearchShow(false);
        }}
        searchOnChange={(e) => {
          myAxios
            .get(
              `/task/production/tpl-search-vehicle-policy?form_type=${state.form_type}&form_action=${state.form_action}&search=${e.target.value}`,
              {
                headers: {
                  Authorization: `Bearer ${user?.accessToken}`,
                },
              }
            )
            .then((res: any) => {
              if (!res?.data.success) {
                return alert(`Error : ${res?.data.message}`);
              }
              setSearch(res?.data.searchVPolicy);
            });
        }}
        height={300}
        isLoading={searchLoading || loadSearchingChange}
        queryKey={vpolicyKey}
        columns={vpolicyColumn}
        onSelectionChange={(rowSelectionModel, data) => {
          if (rowSelectionModel.length <= 0) {
            return;
          }
          const selectedIDs = new Set(rowSelectionModel);
          const selectedRowData = data.filter((row: any) => {
            return selectedIDs.has(row.PolicyNo.toString());
          });

          if (selectedRowData.length <= 0) return;
          const {
            address,
            IDNo,
            Account,
            SubAcct,
            PolicyNo,
            DateIssued,
            TotalPremium,
            Vat,
            DocStamp,
            LGovTax,
            Misc,
            TotalDue,
            AgentID,
            AgentCom,
            CoverNo,
            ORNo,
            DateFrom,
            DateTo,
            Model,
            Make,
            BodyType,
            Color,
            BLTFileNo,
            PlateNo,
            ChassisNo,
            MotorNo,
            AuthorizedCap,
            UnladenWeight,
            PremiumPaid,
            EstimatedValue,
            Aircon,
            Stereo,
            Magwheels,
            Others,
            OthersAmount,
            Deductible,
            Towing,
            RepairLimit,
            BodilyInjury,
            PropertyDamage,
            PersonalAccident,
            SecI,
            SecIIPercent,
            ODamage,
            Theft,
            Sec4A,
            Sec4B,
            Sec4C,
            AOG,
            MortgageeForm,
            Mortgagee,
            Denomination,
            client_fullname,
            agent_fullname,
            TPLTypeSection_I_II,
            AOGPercent,
            LocalGovTaxPercent,
          } = selectedRowData[0];

          function setFormatValue(value: string) {
            const intVal = parseFloat(value);
            if (intVal > 0) {
              return `${value},000.00`;
            }
            return `0.00`;
          }
          function setFixValue(value: string) {
            const intVal = parseFloat(value);
            return intVal.toFixed(2);
          }
          function intToBoolean(value: string) {
            const intVal = parseInt(value);
            return intVal ? true : false;
          }
          handleDateChange(SubAcct, "sub_account");
          handleDateChange(IDNo, "client_id");
          handleDateChange(client_fullname, "client_name");
          handleDateChange(address, "client_address");

          handleDateChange(AgentID, "agent_id");
          handleDateChange(agent_fullname, "agent_name");
          handleDateChange(AgentCom, "agent_com");

          handleDateChange(Account, "PolicyAccount");
          handleDateChange(PolicyNo, "PolicyNo");
          handleDateChange(CoverNo, "CCN");
          handleDateChange(ORNo, "ORN");

          handleDateChange(DateFrom, "DateFrom");
          handleDateChange(DateTo, "DateTo");
          handleDateChange(DateIssued, "DateIssued");

          handleDateChange(Model, "Model");
          handleDateChange(Make, "Make");
          handleDateChange(BodyType, "TB");
          handleDateChange(Color, "Color");
          handleDateChange(BLTFileNo, "BLTFileNo");
          handleDateChange(PlateNo, "PlateNo");
          handleDateChange(ChassisNo, "ChassisNo");
          handleDateChange(MotorNo, "MotorNo");
          handleDateChange(AuthorizedCap, "AuthorizedCapacity");
          handleDateChange(UnladenWeight, "UnladenWeigth");

          handleDateChange(setFixValue(PremiumPaid), "PremiumPaid");
          handleDateChange(setFixValue(EstimatedValue), "EVSV");
          handleDateChange(setFixValue(Aircon), "Aircon");
          handleDateChange(setFixValue(Stereo), "Stereo");
          handleDateChange(setFixValue(Magwheels), "Magwheels");
          handleDateChange(setFixValue(Aircon), "Aircon");
          handleDateChange(setFixValue(OthersAmount), "OthersRate");
          handleDateChange(Others, "OthersDesc");

          handleDateChange(setFixValue(Deductible), "Deductible");
          handleDateChange(setFixValue(Towing), "Towing");
          handleDateChange(setFixValue(RepairLimit), "ARL");
          handleDateChange(setFormatValue(BodilyInjury), "BodyInjury");
          handleDateChange(setFormatValue(PropertyDamage), "PropertyDamage");
          handleDateChange(
            setFormatValue(PersonalAccident),
            "PersinalAccident"
          );
          handleDateChange(setFixValue(SecI), "SectionI_II");
          handleDateChange(setFixValue(SecIIPercent), "SectionIII");
          handleDateChange(setFixValue(ODamage), "OwnDamage");
          handleDateChange(setFixValue(Theft), "Theft");
          handleDateChange(setFixValue(Sec4A), "SectionIVA");
          handleDateChange(setFixValue(Sec4B), "SectionIVB");
          handleDateChange(setFixValue(Sec4C), "PremiumOther");
          handleDateChange(setFixValue(AOG), "AOG");

          handleDateChange(Mortgagee, "Mortgagee");
          handleDateChange(intToBoolean(MortgageeForm), "MortgageeForm");
          handleDateChange(Denomination, "Denomination");

          handleDateChange(setFixValue(TotalDue), "TotalDue");
          handleDateChange(setFixValue(Vat), "Vat");
          handleDateChange(setFixValue(DocStamp), "DocStamp");
          handleDateChange(setFixValue(TotalPremium), "TotalPremium");
          handleDateChange(setFixValue(LGovTax), "LocalGovTax");
          handleDateChange(setFixValue(Misc), "StradCom");

          handleDateChange(
            setFixValue(LocalGovTaxPercent),
            "LocalGovTaxPercent"
          );
          handleDateChange(setFixValue(AOGPercent), "LocaAOGPercent");
          handleDateChange(TPLTypeSection_I_II, "TplType");
          handleDateChange("delete", "mode");
          setSearchShow(false);
        }}
        id="PolicyNo"
        rows={search}
        setRows={setSearch}
      />
    </VehicleContext.Provider>
  );
}
